<template>
    <div class="stepper-box">
        <div class="top">
            <div class="divider-line"></div>
            <div class="steps-wrapper">
                <template v-for="(step, index) in steps">
                    <div :class="['step', isStepActive(index, step)]">
                        <div class="circle">
                            <i class="material-icons md-18">
                                {{ (isStepCompleted(index, step)) ? 'done' : step.icon }}
                            </i>
                        </div>
                        <div class="title">
                            <h4>{{step.title}}</h4>
                            <span class="subtitle">{{step.subtitle}}</span>
                        </div>
                    </div>
                </template>
            </div>
        </div>
        <div class="content">
            <transition
                    :enter-active-class="enterAnimation"
                    :leave-active-class="leaveAnimation"
                    mode="out-in"
            >
                <component :is="steps[currentStep.index].component"></component>
            </transition>
        </div>
        <div :class="['bottom', (currentStep.index > 0) ? '' : 'only-next']">
            <div v-if="currentStep.index > 0" class="stepper-button previous" @click="backStep()">
                <i class="material-icons">keyboard_arrow_left</i>
                <span>Atrs</span>
            </div>
            <div :class="['stepper-button next', !canContinue ? 'deactivated' : '']" @click="nextStep()">
                <span>Siguiente</span>
                <i class="material-icons">keyboard_arrow_right</i>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            steps: {
                type: Array,
                default: function () {
                    return [
                        {
                            icon: 'mail',
                            name: 'first',
                            title: 'Sample title 1',
                            subtitle: 'Subtitle sample'

                        },
                        {
                            icon: 'report_problem',
                            name: 'second',
                            title: 'Sample title 2',
                            subtitle: 'Subtitle sample'
                        }
                    ]
                }
            },
            canContinue: {
                default: true
            }
        },
        data() {
            return {
                completedSteps: [],
                currentStep: {},
                previousStep: {}
            }
        },
        computed: {
            enterAnimation() {
                if (this.currentStep.index < this.previousStep.index) {
                    return 'animated quick fadeInLeft'
                } else {
                    return 'animated quick fadeInRight'
                }
            },
            leaveAnimation() {
                if (this.currentStep.index > this.previousStep.index) {
                    return 'animated quick fadeOutLeft'
                } else {
                    return 'animated quick fadeOutRight'
                }
            }
        },
        methods: {
            isStepActive(index, step) {
                if (this.currentStep.index === index) {
                    return 'activated'
                } else {
                    return 'deactivated'
                }
            },
            isStepCompleted(index, step) {
                if (this.completedSteps.includes(step.name)) {
                    return true
                }
            },
            activateStep(index) {
                this.previousStep = this.currentStep;
                this.currentStep = {
                    name: this.steps[index].name,
                    index: index
                };
                // Emit current step
                this.$emit('active-step', this.currentStep);
            },
            nextStep() {
                if (this.canContinue) {
                    let currentIndex = this.currentStep.index + 1;
                    this.activateStep(currentIndex);
                }
            },
            backStep() {
                let currentIndex = this.currentStep.index - 1;
                if (currentIndex >= 0) {
                    this.activateStep(currentIndex);
                }
            }
        },
        created() {
            // Initiate stepper
            this.activateStep(0);
        }
    }
</script>

<style src="./HorizontalStepper.scss" scoped lang="scss"></style>


